<template>
  <div class="big-table-wrap" ref="tableWrap">
    <!--操作按钮区域-->
    <div class="serarch-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
      <div class="handle-btn-wrap">
        <Button type="primary" @click="reSearchInfo" >查询</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  export default {
    data () {
      return {
        loading:false,
        /*table表格*/
        tableColumns: [
          {
            title: '活动期数',
            align: 'center',
            key: 'rule_id'
          },
          {
            title: '用户号',
            key: 'user_no',
            align: 'center',
            width:150
          },
          {
            title: '排名',
            key: 'order_no',
            align: 'center',
          },
          {
            title: '持有量',
            align: 'center',
            key: 'account_price'
          }
        ]
      }
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //查询数据
        // this.searchInfo();
      })
    },
    methods: {
      //查询数据
      searchInfo(){
        this.loading=true;
        let jsonData = protocol.getJsonParams(protocol.queryRanking, '');
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.tableData = data.returnList;
        })
      },
      //点击查询
      reSearchInfo(){
        this.searchInfo();
      },
      //导出数据
      exportOk(){
        let jsonData = protocol.getJsonParams(protocol.exportRanking, '');
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>




